import React from 'react'

import Box from '../../components/Box'
import Text from '../../components/Text'
import Container from '../../components/Container'
import Image from '../../components/Image'

import info from './info.svg'
import coins from './coins.svg'
import { responsive } from '../../components/ThemeProvider/theme'

const Info = () => {
  return (
    <Container py="3em">
      <Image src={info} />
      <Box py="2em" fontSize={responsive('0.75em', '1em')}>
        * ( RR1RR2RR3RR4RR5 ) RR3
      </Box>
      <Image src={coins} />
      <Box py="2em">
        <Text.Inline color="noteGreen" fontSize={responsive('0.75em', '1em')}>
          **()NA (N)NB(N)()
        </Text.Inline>
        <Text fontSize={responsive('0.75em', '1em')}>100%http://www.schroders.com.tw</Text>
      </Box>
    </Container>
  )
}

export default Info
